<template>
	<view class="page-home all df fdc">
		<!-- 头部 -->
		<div class="head df aic jcsb">
			<div class="head-left df aic">
				<image class="wh32 mr8" src="../../static/fonts/user-black.svg" mode=""></image>
				<p class="fz18 fwb">现金账户A</p>
			</div>
			<div class="head-right df aic">
				<image class="wh24 mr32" src="../../static/fonts/Headphone-black.svg" mode=""></image>
				<image class="wh24" src="../../static/fonts/SMS-black.svg" mode=""></image>
			</div>
		</div>
		<!-- 钱包 -->
		<div class="wallet">
			<div class="total df jcsb">
				<div class="df jcc fdc">
					<p class="fff fz12 mb4">钱包总额</p>
					<p class="fz24 sign fwb">$56009.00</p>
				</div>
				<div class="avator"></div>
			</div>
			<!-- 可用余额 -->
			<div class="balance df aic jcsb">
				<p class="fz12">可用余额</p>
				<p class="fz16 fwb">$4000.00</p>
			</div>
		</div>
		<!-- 仪表组件 -->
		<scroll-view :scroll-x="true">
			<div class="dashboard md16 df jcc jcse">
				<div class="dashboard-item df fdc aic jcc" v-for="(item,index) in circleData" :key="index">
					<div :id="item.className" class="circle"></div>
					<p class="fz14">{{item.name}}</p>
					<p class="fz16 fwb">{{item.price}}</p>
				</div>
			</div>
		</scroll-view>
		<!-- 热门股票 -->
		<view style="color: black;font-weight: 800;font-size: 30rpx;margin: 20rpx;">热门股票</view>
		<view class="skots_list">
			<view class="scokt_item" v-for="(item,index) in scoktList" :key="index">
				<div class="df aic">
					<image src="../../static/fonts/B.svg"></image>
					<view class="scokt_left">
						<text>名称</text>
						<text>类型(股票/指数等)</text>
					</view>
				</div>
				<view :id="item.className" class="myCharts2" style="width: 124rpx;height:80rpx;"></view>
				<view class="scokt_right">
					<text>198</text>
					<text :class="item.plus=='+'?'sign':'redss'">+1(+0.53%)</text>
				</view>
			</view>
		</view>
		<!-- <button @click="goTest">点击跳转到test</button> -->
	</view>
</template>

<script>
	import * as echarts from 'echarts';
	import {
		render
	} from "vue";
	export default {
		data() {
			return {
				// 行内chrts图表数据
				scoktList: [{
					"className": "sChart1",
					"name": "股票1",
					"float": "+1(+0.56%)",
					"plus": "+"
				}, {
					"className": "sChart2",
					"name": "股票2",
					"float": "-1(-0.56%)",
					"plus": "-"
				}, {
					"className": "sChart3",
					"name": "股票3",
					"float": "-1(-1.6%)",
					"plus": "-"
				}, {
					"className": "sChart4",
					"name": "股票4",
					"float": "+1(+0.56%)",
					"plus": "+"
				}],
				// 仪表盘数据
				circleData: [{
						className: "circle1",
						name: '股票',
						price: '$23,760'
					},
					{
						className: "circle2",
						name: '指数',
						price: '$23,760'
					},
					{
						className: "circle3",
						name: '原油',
						price: '$23,760'
					},
					{
						className: "circle4",
						name: '股票',
						price: '$23,760'
					},
					{
						className: "circle5",
						name: '指数',
						price: '$23,760'
					},
					{
						className: "circle6",
						name: '原油',
						price: '$23,760'
					}
				]
			}
		},
		methods: {
			goTest() {
				uni.navigateTo({
					url: '/pages/test/test'
				})
			},
			render2() {
				var that = this;
				// 基于准备好的dom，初始化echarts实例
				for (var i = 0; i < that.scoktList.length; i++) {
					var chart = that.scoktList[i]

					var myChart = echarts.init(document.getElementById(chart.className));
					let options = {
						tooltip: {},
						grid: {
							left: '0', //距左边边框的距离
							right: '0', //距右边边框的距离
							bottom: '0', //距下面边框的距离
							top: '0', //距上面边框的距离
							containLabel: true,
						},
						xAxis: {
							data: [],
							axisLine: {
								show: false, //隐藏y轴
							},
						},
						yAxis: {
							axisTick: {
								show: false, //刻度线
							},
							axisLine: {
								show: false, //隐藏y轴
							},
							axisLabel: {
								show: false, //隐藏刻度值
							},
							splitLine: {
								show: false,
								lineStyle: {
									type: 'dashed',
								},
							},
						},
						series: [{
							// name: '销量',
							type: 'line',
							data: [10, 9, 12, 20, 15, 13, 18, 20, 20, 10, 10, 15, 13, 23, 20, 12, 16, 20, 20,
								10, 9, 10, 13, 18, 10, 20, 13, 10, 19
							],
							smooth: true,
							showSymbol: false, //隐藏线形上面的圆点
							lineStyle: {
								color: chart.plus == "+" ? '#12d18e' : '#ffd1d1',
							},
							// 颜色渐变
							areaStyle: {
								normal: {
									color: new echarts.graphic.LinearGradient(
										0, 0, 0, 1,
										[{
												offset: 0,
												color: chart.plus == "+" ? 'rgba(18,209,142,0.9)' :
													'rgba(208,2,27,0.9)'
											},
											{
												offset: 1,
												color: chart.plus == "+" ? 'rgba(18,209,142,0)' :
													'rgba(208,2,27,0)'
											}
										]
									)
								}
							},
						}]
					}
					// 绘制图表
					myChart.setOption(options);
				}
			},
		},
		// 页面挂在之后执行渲染函数。
		mounted() {
			var that = this;
			for (var i = 0; i < that.circleData.length; i++) {

				var chartDom = that.circleData[i]

				var myChart = echarts.init(document.getElementById(chartDom.className));
				var option;
				option = {
					title: {
						text: 56 + '%',
						top: 'center',
						left: 'center',
						textStyle: {
							fontSize: 16,
							color: '#666666'
						}
					},
					series: [{
						name: 'Access From',
						type: 'pie',
						radius: ['80%', '100%'],
						hoverAnimation: false,
						silent: true,
						label: {
							show: false,
							position: 'center'
						},
						// 逆时针
						// clockwise: false,
						startAngle: 235,
						data: [{
							value: 70,
							itemStyle: {
								color: '#12d18e'
							}
						}, {
							value: 100 - 70,
							itemStyle: {
								color: '#fff'
							}
						}]
					}]
				};
				option && myChart.setOption(option);
			}

		},
		onLoad() {},
		onShow() {
			this.$nextTick(() => {
				this.render2();
			})
		},
		onResize() {},
	}
</script>

<style scoped lang="scss">
	.page-home {
		width: 750rpx;
	}

	// 头部样式
	.head {
		margin-bottom: 32rpx;
	}

	// 钱包样式
	.wallet {
		width: 686rpx;
		height: 308rpx;
		border-radius: 20rpx;
		background-color: #031c10;
		margin-bottom: 28rpx;
	}

	.total {
		padding: 28rpx 48rpx 0;
		margin-bottom: 40rpx;
	}

	.avator {
		width: 112rpx;
		height: 112rpx;
		background: #d9d9d9;
	}

	.balance {
		width: 616rpx;
		height: 80rpx;
		background: #1c3328;
		color: #fff;
		border-radius: 10rpx;
		padding: 0 32rpx;
		margin: auto;
	}

	// 仪表盘样式
	.dashboard {
		width: 1388rpx;
		height: 268rpx;
	}

	.circle {
		width: 120rpx;
		height: 120rpx;
	}

	.dashboard-item {
		margin: 0 40rpx;
	}

	// 行内图标样式
	.myCharts2 {
		width: 124rpx;
		height: 80rpx;
		flex: none;
	}

	.redss {
		color: red;
	}

	.scokt_list {
		display: flex;
		flex-direction: column;
	}

	.scokt_item {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 20rpx;
	}

	.scokt_item image {
		width: 104rpx;
		height: 104rpx;
	}

	.scokt_left {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		margin-left: 20rpx;
	}

	.scokt_left text:nth-child(odd) {
		color: black;
		font-size: 30rpx;
		font-weight: 800;
		margin-bottom: 10rpx;
	}

	.scokt_left text:nth-child(even) {
		color: #999999;
		font-size: 12px;
	}

	.scokt_right {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
	}

	.scokt_right text:nth-child(odd) {
		color: black;
		font-size: 30rpx;
		font-weight: 800;
		margin-bottom: 8rpx;
	}

	.scokt_right text:nth-child(even) {
		font-size: 24rpx;
	}
</style>